<script setup>

//菜单数据
import { ref, onMounted, onUnmounted, nextTick } from 'vue'
import menu from '@/static/data/menu.json'
import menuitem from '@/static/data/menuitems.json'

const menuData = menu
const isshow = ref(false)

const miData = menuitem


const indexxx = ref('')
function shows(value) {
    indexxx.value = Number(value + 1)

    nextTick(() => {
        if (two.value&&hidden.value) {
            const height = two.value.clientHeight
     
          
            const erji = hidden.value.clientHeight
            const erjihhh = document.getElementsByClassName('hidden')
         
            if(height > erji){
              
                erjihhh[0].style.height = '850px'
            }else{
                erjihhh[0].style.height = '500px'
             
            }
        }
    })

}
const hidden = ref()
const two = ref()
onMounted(() => {
    const aaa = document.addEventListener('scroll', function () {
        if (isshow.value == false) {
           
            document.removeEventListener('scroll', aaa)
        } else {
            const gun = document.getElementsByClassName('hidden')
            if (window.scrollY >= 230) {
                gun[0].style.position = 'fixed'
                gun[0].style.zIndex = '1000'
                gun[0].style.left = '20%'
            } else {
                gun[0].style.position = 'absolute'
                gun[0].style.left = '' 
            }
        }
    })
})
</script>
<template>
    <div class="menu" id="menu">
        <div class="menu-box" @mouseenter="isshow = true" @mouseleave="isshow = false">
            <ul>
                <li v-for="(items, index) in menuData" @mouseenter="shows(index)" class="lis">
                    <!-- 图标 -->
                    <img :src="items.iconpath" alt="" class="i-menu">
                    <template v-for="(item, idx) in items.name">
                        <!-- 一级菜单 -->
                        <span class="menu_item_name">{{ item }}</span>
                        <span v-if="idx < items.name.length - 1">/</span>
                    </template>
                </li>
            </ul>
            <div>
                <div class="hidden" v-if="isshow" id="hidden" ref="hidden">
                    <!-- 二级菜单 -->
                    <div class="two" ref="two">
                        <div v-for="item in miData['inner' + indexxx]" class="erjimenu">
                            <div class="menu_item">
                                <div class="title_one">
                                    <text class="menu-title">{{ item.title }}</text>
                                    ❯
                                </div>
                                
                               <div class="qqq">
                                 <text v-for="i in item.sub_items" class="menu_item_item">
                                    {{ i.title }}&nbsp;&nbsp;&nbsp;&nbsp;
                                </text>
                               </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<style>
.erjimenu{
    padding-left: 10px;
    padding: 5px;
}
.qqq{
    display: flex;
    flex-wrap: wrap;
    
  
 
}
.title_one{
    position: relative;
     min-width: 100px;
    
    display: flex;
   justify-content: space-between;
   
    
}
.menu_item_item {
    font-size: 14px; 
    font-weight: 300;
   
}
.menu-title{
   
 
    font-weight: bold;
   
}
.two {
    width: 100%;
    height: auto;
   
     display: flex;
     flex-direction: column;
     
}
.menu_item {
    display: flex;
    gap: 10px;
   
  
}

.i-menu {
    height: 17px;
    width: 15px;
    padding: 0 4px;

}

.menu-box {
    height: 440px;
    width: 100%;
    overflow: hidden;
}
.menu-box:hover {
    height: auto;
    overflow: visible;
}
ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}
a {
    text-decoration: none;
}
.lis {
    background-color: #f7f8fc;
    height: 39px;
    width: 100%;
    display: flex;
    font-size: 19px;
    align-items: center;
}
span {
    padding: 0 2px;
    font-size: 12px;
}
.lis:hover {
    background-color: #d9d9d9;
}
.menu_item_name:hover {
    color: red;
    text-decoration: underline;

}

.menu_item_name {
    font-size: 15px;
}

.hidden {
    position: absolute;
    width: 1000px;
    top: 0;
    height: 500px;
    border-radius: 5px;
    left: 248px;
    display: block;
    border: 1px solid rgb(255, 0, 0);

    background-color: #ffffff;
}

.menu {
    position: relative;
    display: flex;
    flex-direction: row;

    background-color: #f7f8fc;
    z-index: 988;

}
</style>