<script setup>
import { ref,onMounted,onBeforeUnmount } from 'vue'
const imgs = [{
    src: require('@/assets/hh/image.webp')
},
{ src: require('@/assets/hh/11d26534c48cb767.webp') },
{
    src: require('@/assets/hh/57b627dc27035eee.webp')
}]
const currindex = ref(0);
let timer = null;
const startAutoPlay = () => {
  timer = setInterval(() => {
    currindex.value = (currindex.value + 1) % imgs.length;
    
  }, 3000);
};
onMounted(startAutoPlay);
onBeforeUnmount(() => clearInterval(timer));
function swiperleft(){
    currindex.value = (currindex.value - 1 + imgs.length) % imgs.length;
}
function swiperright(){
    currindex.value = (currindex.value+1)% imgs.length
}
</script>
<template>
    <div class="swiper-li">
        <ul>

            <li v-for="(item, index) in imgs" :key="index" class="swiper-item">
                <img :src="item.src" :style="{opacity:currindex === index ? 1 : 0}" alt="渲染失败">
            </li>

        </ul>
        <div class="swiper-button-left" @click="swiperleft">❮</div>
        <div class="swiper-button-right" @click="swiperright">❯</div>

        <ul class="dot">
            <li v-for="(item,i) in imgs.length" :class="{active:currindex === i}" @click="currindex = i"></li>
        </ul>

    </div>
</template>
<style>
.dot {
    width: auto;
    z-index: 100;
    display: flex;
    position: absolute;
    bottom: 20px;
    left: 20px;

}

.dot li {
    width: 9.33px;
    height: 9.33px;
    border-radius: 100%;
    background-color: rgb(234, 234, 234);
    margin: 3px;
}
.dot li.active{
    width: 17.33px;
    background-color: white;
    border-radius: 15px 15px 15px 15px;
}
.dot li:hover {
    width: 17.33px;
    background-color: white;
    border-radius: 15px 15px 15px 15px;
}

.swiper-button-left {
    width: 30px;
    height: 30px;
    z-index: 100;

    color: #00000000;
    border-radius: 0 15px 15px 0;
    text-align: center;
    margin-top: 78px;
    cursor: pointer;

}

.swiper-li:hover .swiper-button-left {
    width: 30px;
    height: 30px;
    background-color: #00000050;
    color: white;
}

.swiper-li:hover .swiper-button-right {
    width: 30px;
    height: 30px;
    background-color: #00000050;
    color: white;
}

.swiper-button-right {
    position: absolute;
    width: 30px;
    height: 30px;
    z-index: 100;

    color: #00000000;
    border-radius: 15px 0 0 15px;
    text-align: center;
    margin-top: 78px;
    right: 0;
cursor: pointer;

}

img {
    width: 468px;
    height: 180px;
   transition: opacity 0.5s ease;
   border-radius: 8px;
}



.swiper-li {
    display: flex;
    max-width: 490px;
    height: 180px;
    position: relative;

}

.swiper-item {
    position: absolute;
    top: 0;
}
</style>