<script setup>
import words from '@/static/data/hotwords.json'
import { ref, onMounted } from 'vue'
const LiftIsShow = ref(false)
const wordsdata = words
//吸顶计算高度
const searchmIsShow = ref(false)

const jdHeight = defineProps({
    tojdcontentHeight: Number
})
function send() {
    emit('liftshow', LiftIsShow)
}
const emit = defineEmits(['liftshow'])
onMounted(() => {
    const wbody = document.getElementsByClassName('w-body')
    const wbodyHeight = wbody[0].offsetHeight
    let timer = null
    document.onscroll = function () {
        //节流处理
        if (timer !== null) {
            return
        }
        timer = setTimeout(() => {
            let top = window.scrollY || document.documentElement.scrollTop;

            if (top >= wbodyHeight + jdHeight.tojdcontentHeight) {
                searchmIsShow.value = true
                LiftIsShow.value = true;
                send()
            } else {
                searchmIsShow.value = false
                LiftIsShow.value = false
                send()
            }
            timer = null
        }, 200)

    }
})
</script>
