            <div class="logo-box">
                <img src="../assets/c107eadb786b0977.webp" alt="" style="width: 100%; height: auto;margin-top: 15%;">
            </div>

        </div>
        <div class="search">
            <div class="search-button">
                <input type="text" class="top-input" placeholder="热门商品" aria-label="搜索">
                <button aria-label="搜索" class="button" id="button">搜索</button>

            </div>
            <div class="hotwords">
                <ul>
