<script setup>
const iconShows = [
  {iconPath : require('@/assets/lifticon/96370b055a4906d2.webp'),name:'购物车'},
  {iconPath : require('@/assets/lifticon/8ff222a4f372cbf2.webp'),name:'我的'},
  {iconPath : require('@/assets/lifticon/d0f9b0d422148dad.webp'),name:'客服'},
  {iconPath : require('@/assets/lifticon/62bf274636f98833.webp'),name:'反馈'},
  {iconPath : require('@/assets/lifticon/回到顶部.webp'),name:'回顶部'} 
]
defineProps({
  toLift:Boolean  
})
 
// 新增滚动函数
const scrollToTop = () => {
  window.scrollTo({
    top: 0,
    behavior: 'smooth'
  });
}
const lt = document.getElementsByClassName('lift-text')
function textcolor(value){
    lt[value].style.color = '#fff'
}
function textclose(value){
    lt[value].style.color = 'black'
}
</script>
<template>
  <div class="lift-body">
    <div class="lift-items">
      <div class="lift-item" v-for="(item,index) in iconShows.slice(0,4)" @mouseenter="textcolor(index)" @mouseleave="textclose(index)">
        <a href >
          <img :src="item.iconPath" alt="">
          <span class="lift-text">{{ item.name }}</span>  
        </a>
      </div>
      <div class="lift-item"  v-if="toLift" @mouseenter="textcolor(4)" @mouseleave="textclose(4)">
      
        <a href @click.prevent="scrollToTop">
          <img :src="iconShows[4].iconPath" alt="">
          <span class="lift-text">{{ iconShows[4].name }}</span>  
        </a>
      </div>
    </div>
  </div>
</template>
<style>

.lift-items{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
.lift-item>a>span{
    color: black;
}
.lift-item a>img{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.lift-item a{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 12px;
}
.lift-item a>span{
  padding:16px 4px;
 
}
.lift-item a>img{
  position: relative;
  top: 10px;
}
.lift-item{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #ffffff;
}
.lift-items :hover{
  
  background-color: red;
}
</style>