<script setup>
const adv = document.getElementsByClassName('adv')

function advShow(){
adv[0].style.zIndex = '999'
}
function advHidden(){
    adv[0].style.zIndex = '0'
}
</script>
<template>
    <div class="adv" @mouseenter="advShow()" @mouseleave="advHidden">
        <div class="adv-left">
            <img src="../assets/adv/c8c3eed4df9b15d4.webp" alt="" >
        </div>
        <div class="adv-right">
            <img src="../assets/adv/49fe443ae4d03337.webp" alt="">
            <div class="close">
                <img src="../assets/叉.webp" alt="" @click="advHidden"></img>
            </div>
        </div>
    </div>
</template>
<style>
.close>img{
    width: 25px;
    height: 25px;
    position: absolute;
    
    top: 200px;
    left: 800px;
    background-color: rgb(106, 104, 104);
}
.adv-left{
    position: absolute;
    left: 0;
}
.adv-left>img{
    position: absolute;
    width: 100px;
    height: 472px;
    top: 170px;
  
}
.adv-right{
    position: absolute;
    left: 100px;
}
.adv-right>img{
    position: absolute;
    height: 472px;
    width: auto;
    top: 170px;
    transition: transform 2s ease;
}
.adv{
    cursor: pointer;
    position: absolute;
    top: 60px;
    left: 0;
    
    
}
</style>