<script setup>
import {ref,onMounted} from 'vue'
    import feeds from '@/static/data/feeds.json'
    import cards from '@/static/data/cards.json'
    const cardsData = cards
    const feedsData = feeds
    const cardindex = ref('1')
onMounted(()=>{
     const sf = document.getElementsByClassName('selectfeeds')
      sf[0].style.backgroundColor = "#ffebf1";
})
    function selectitem(value){
        cardindex.value = value
      
        const sf = document.getElementsByClassName('selectfeeds')
          const index = Number(value-1);
          

          //重置
           for (let i = 0; i < sf.length; i++) {
    sf[i].style.backgroundColor = ""; 
    sf[i].style.fontWeight = "";
    sf[i].style.fontSize = "";
  }
  if (sf[index] && index >= 0 && index < sf.length) {
     sf[index].style.backgroundColor = "#ffebf1";
    //  sf[index].style.color = "#FF0F23"
     sf[index].style.fontWeight = "500"
     sf[index].style.fontSize = "16px"
  }
    }
    
    
</script>
<template>
    <div>
        <div class="feeds">
            <ul> 
                <li v-for="(item,index) in feedsData.inner" :key="item.id" @click="selectitem(item.id)" class="selectfeeds">
                    <img :src="item.imgpath" alt="">
                    <span>{{ item.name }}</span>
                </li>
            </ul>
    </div>
    <div class="gl" v-for="round in 3">
        <div class="goodslist" v-for="(item,index) in cardsData['inner' + cardindex]">
        <div class="goods-img">
            <img :src="item.path" alt="数据未写入">
        </div>
        <div class="goods-info">
            <p>{{ item.title }}</p>
        </div>
        <div class="price">
            <i>¥</i>
            <span class="price-info">
                {{ item.price }}.
                <span class="price-info-xiaoshu">{{ item.dec }}</span>
            </span>
        </div>
    </div>
    </div>
    </div>
</template>
<style>
.gl{
    
    display: flex;
    flex-wrap: wrap;
}
.feeds>ul{
    display: flex;
    padding: 15px;
    gap: 15px;
}
.feeds>ul>li{
    width: 108px;
    height: 36px;
    border-radius: 10px;
    background-color: #f7f8fc;
    display: flex;
    align-items: center;
    justify-content: center;
}
.feeds>ul>li>img{
    height: 24px;
    width: 24px;
}
.feeds>ul>li>span{
    font-size: 16px;
    color: #505259;
}
.feeds>ul>li>span:hover{
    color: red;
}
.goodslist:hover{
    border: 1px solid red;
    border-radius: 10px;
    cursor: pointer;
}
i{
  width: 9px;
  height: 14px;
  font-weight: 500;
}
.price {
  color: #ff0f23;
  display: flex;
  align-items: baseline;
  line-height: 1;
}
 
.price-info {
  font-size: 24px;
  font-weight: 500;

  min-height: 24px; 
  display: inline-flex; 
}
 
.price-info-xiaoshu {
  font-size: 16px;

  align-self: flex-end;

  margin-bottom: -2px; 
}
.goodslist{
    width: 253px;
    height: 373px;
    z-index: 5;
    
}
.goods-img img{
    width: 240px;
    height: 240px;
    padding: 6px;
}
.goods-img{
    display: flex;
}
p{
    width: 245px;
    height: 48px;
    display: -webkit-box;    
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2; 
  overflow: hidden;  

 
  

}
</style>