<script setup>
import rba from '@/static/data/Rb.json'
import { ref } from 'vue'
const rbData = rba

const leftbtnIsShow = ref(false)
const rightbtnIsShow = ref(true)
const rb = document.getElementsByClassName('rb')
function toRight(){
    leftbtnIsShow.value = true
    rightbtnIsShow.value = false
    rb[0].style.right = '200px'
}
function toLeft(){
    leftbtnIsShow.value = false
    rightbtnIsShow.value = true
    rb[0].style.right = '0'
}


</script>
<template>
    <div class="rb-btn">
        <div class="rb-left-btn" v-if="leftbtnIsShow" @click="toLeft">
            ❮
        </div>

        <div class="rb-right-btn" @click="toRight" v-if="rightbtnIsShow">
            ❯
        </div>
    </div>
    <div class="rb-warp">
        <div class="rb">
            <div class="rb-warp-top">
            <a href="javascript:void(0)" v-for="item in rbData.top" class="rb-a">
                <img :src="item.path" alt="" class="rb-img">
                <span>{{ item.name }}</span>
            </a>
        </div>
        <div class="rb-warp-btm">
            <a href="javascript:void(0)" v-for="item in rbData.bottom" class="rb-a">
                <img :src="item.path" alt="" class="rb-img">
                <span>{{ item.name }}</span>
            </a>
        </div>
        </div>
    </div>
</template>
<style>
.rb-left-btn {
    position: absolute;
    top: 40%;
    z-index: 999;
    width: 20px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: rgba(0, 0, 0, 0.265);
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    cursor: pointer;
}

.rb-right-btn {
    position: absolute;
    z-index: 999;
    top: 40%;
    right: 0;
    width: 20px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    background-color: rgba(0, 0, 0, 0.249);
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    cursor: pointer;
}

.rb-btn {
    position: absolute;
    width: 248px;
    height: 112px;
}

.rb-warp-btm {
    display: flex;
    
}

.rb-img {
    height: 28px;
    width: 28px;
}

.rb-a {
    display: flex;
    align-items: center;
    flex-direction: column;
    min-width: 65px;
    height: 44px;

}

.rb-a>span {
    font-style: 12px;
    color: #505259;
}

.rb-warp {

    position: relative;
    overflow: hidden;
    
}

.rb-warp-top {

    display: flex;
   
    
    position: relative;
  
   
}
.rb{
    display: flex;
    
    justify-content: center;
    flex-direction: column;
    position: relative;
    padding: 9px;
   
}
</style>