<script setup>

import { ref, onMounted ,onBeforeUnmount,onUnmounted} from 'vue'
const lbisshow = ref(true)


onMounted(() => {
    document.addEventListener('scroll', gun)
})
onBeforeUnmount(() => {
        document.removeEventListener('scroll', gun)
})
function gun() {
    lbisshow.value = false
    let timeout = null
    timeout = setTimeout(() => {
        lbisshow.value = true
    }, 1000)
}  

//控制组件卸载
const loginbottom = ref(true) 
const emit = defineEmits('close')
function closelb() {
    lbisshow.value = false
    emit('close',lbisshow.value)
}
</script>
